import styles from "./page.module.css"

const slides = new Array(5).fill(null)

export default function Page() {
  return (
    <section className={styles.stage}>
      <div className={styles.carousel} aria-hidden="true">
        {slides.map((_, index) => (
          <div key={index} className={styles.slide} />
        ))}
      </div>
      <div className={styles.blurOverlay} aria-hidden="true" />
      <div className={styles.content}>
        <span className={styles.word}>Etch</span>
        <p className={styles.subtitle}>
          Clean, pure CSS animation that deepens the typography while the abstract backdrops
          drift through five distinct moods.
        </p>
      </div>
    </section>
  )
}
